import axios from "axios"
import {url}  from '../url'



//  Axios
{/* <script src="https://unpkg.com/axios/dist/axios.min.js"></script> */}

//  baseUrl
const reqObj = axios.create({
  // baseURL  IP:server (ex. http://192.168.0.112:3000)
	// baseURL: 'http://10.0.102.245:3001',
	// baseURL: 'http://192.168.0.101:3001',
	baseURL: url,
	header: {
		'Content-Type': 'application/json'
	}
})

//  promise
function handleReq(e) {
	return new Promise((resolve, reject) => {
		e
			.then((res) => resolve(res.data))
			.catch((err) => reject(err))
	})
}

// api function
export function getCarousel(id) {
	return handleReq(reqObj.get('/admin/home/carousel/get'))
}


