import React, { Component } from 'react';
import '../activities_components.css';

const ActClass = ({ listData, hello }) => {
    const actClass1 = () => {
        let filterData = listData.filter((e) => e.act_class == 1)
        let classTag = document.querySelectorAll('.custom-title')[0]
        classTag.textContent = 'LIVE'
        classTag = document.querySelectorAll('.title-shadow')[0]
        classTag.textContent = 'LIVE'
        classTag = document.querySelectorAll('.title-bg')[0]
        classTag.textContent = 'LIVE'
        hello(filterData)
    }
    const actClass2 = () => {
        let filterData = listData.filter((e) => e.act_class == 2)
        let classTag = document.querySelectorAll('.custom-title')[0]
        classTag.textContent = ''
        classTag = document.querySelectorAll('.title-shadow')[0]
        classTag.textContent = ''
        classTag = document.querySelectorAll('.title-bg')[0]
        classTag.textContent = ''
        hello(filterData)
    }
    const actClass3 = () => {
        let filterData = listData.filter((e) => e.act_class == 3)
        let classTag = document.querySelectorAll('.custom-title')[0]
        classTag.textContent = ''
        classTag = document.querySelectorAll('.title-shadow')[0]
        classTag.textContent = ''
        classTag = document.querySelectorAll('.title-bg')[0]
        classTag.textContent = ''
        hello(filterData)
    }
    const actClass4 = () => {
        let filterData = listData.filter((e) => e.act_class == 4)
        let classTag = document.querySelectorAll('.custom-title')[0]
        classTag.textContent = ''
        classTag = document.querySelectorAll('.title-shadow')[0]
        classTag.textContent = ''
        classTag = document.querySelectorAll('.title-bg')[0]
        classTag.textContent = ''
        hello(filterData)
    }
    const actClass5 = () => {
        let filterData = listData.filter((e) => e.act_class == 5)
        let classTag = document.querySelectorAll('.custom-title')[0]
        classTag.textContent = ''
        classTag = document.querySelectorAll('.title-shadow')[0]
        classTag.textContent = ''
        classTag = document.querySelectorAll('.title-bg')[0]
        classTag.textContent = ''
        hello(filterData)
    }

    return(
        <div>
            <div id="activeType" className="titleSW"></div>
            <div className="btnSelect">
                <button onClick={actClass1} className="type-btn btn-live">LIVE</button>
                <button onClick={actClass2} className="type-btn btn-popup"></button>
                <button onClick={actClass3} className="type-btn btn-car"></button>
                <button onClick={actClass4} className="type-btn btn-market"></button>
                <button onClick={actClass5} className="type-btn btn-forum"></button>
            </div>
        </div>
    )
}

export default ActClass;


